import React from "react";

function Employee(props) {
  return (
    <div>
      {props.employees
        .sort(function (a, b) {
          var nameA = a.name.last;
          var nameB = b.name.last
          if (props.sort == "asc") {
            var list = nameB-nameA;
            console.log("list: ", list);
            return nameB-nameA;
          } else if (props.sort == "desc") {
            // console.log(a.name.last-b.name.last);
            var list = nameA-nameB;
            console.log("list: ", list);
            return nameA-nameB;
          }
        })
        .filter((employee) => {
          if (props.search == "") {
            return employee;
          } else if (
            employee.name.first
              .toLowerCase()
              .includes(props.search.toLowerCase()) ||
            employee.name.last
              .toLowerCase()
              .includes(props.search.toLowerCase())
          ) {
            return employee;
          }
        })
        .map((employee) => (
          <div>
            <ul>
              <li>
                Name: {employee.name.first} {employee.name.last}
              </li>
              <li>Email: {employee.email}</li>
              <li>Phone: {employee.cell}</li>
              {/* <li>{employee.location}</li> */}
            </ul>
          </div>
        ))}
    </div>
  );
}
export default Employee;
